    'Hiragino Mincho ProN', 'HG\660E\671D  E', '\FF2D\FF33   \FF30\660E\671D', '\FF2D\FF33   \660E\671D', serif;
  -webkit-text-size-adjust: 100%;
  color: #090707;
}
/*---------- a ----------*/
a {
  color: #30a9de;
}
a:hover {
  opacity: 0.7;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
/*---------- fade ----------*/
.fade-enter-active {
  -webkit-transition: opacity 1s ease-in;
  transition: opacity 1s ease-in;
}
.fade-leave-active {
  -webkit-transition: opacity 0.1s ease-out;
  transition: opacity 0.1s ease-out;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
/*---------- formstyle ----------*/
input,
button,
textarea,
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
/*---------- spOnly ----------*/
.spOnly {
  display: none;
}
/*======================================
   
header
  
=======================================*/
header {
  position: fixed;
  top: 0;
  z-index: 100;
  width: 100%;
  background: #30a9de;
}
header:after {
  position: absolute;
  bottom: -5px;
  left: 0;
  display: block;
  width: 100%;
  height: 5px;
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(rgba(0, 0, 0, 0.4)),
    to(rgba(0, 0, 0, 0))
  );
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.4) 0%,
    rgba(0, 0, 0, 0) 100%
  );
  content: '';
}
/*---------- nav ----------*/
header nav {
  width: 620px;
  margin: auto;
}
/* ul */
header nav ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
header nav ul li {
  padding: 10px 20px;
  position: relative;
}
header nav ul li:not(:first-child):before {
  content: '';
  display: block;
  width: 1px;
  height: 20px;
  background: #fff;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  margin: auto 0;
}
header nav ul li a {
  color: #fff;
  text-decoration: none;
}
/*======================================
   
main
  
=======================================*/
main {
  margin: 37px auto 75px;
  width: 620px;
}
/*---------- h1 ----------*/
main h1 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  text-align: center;
  height: 150px;
  position: relative;
  margin: 0 0 75px;
}
main h1 span {
  display: block;
  font-size: 40px;
  font-family: NotoSerif, '\6E38\660E\671D', YuMincho, '\30D2\30E9\30AE\30CE\660E\671D   ProN W3',
    'Hiragino Mincho ProN', 'HG\660E\671D  E', '\FF2D\FF33   \FF30\660E\671D', '\FF2D\FF33   \660E\671D', serif;
}
main h1 small {
  padding: 1.25em 0 0;
  font-size: 15px;
}
main h1:after {
  content: '';
  display: block;
  background: #090707;
  height: 2px;
  width: 50px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
}
/*---------- h2 ----------*/
main h2 {
  text-align: center;
  font-size: 25px;
}
main h2:after {
  content: '';
  display: block;
  margin: 15px auto;
  background: #090707;
  width: 1px;
  height: 25px;
}
/*---------- article ----------*/
main article:not(:first-of-type) {
  margin-top: 75px;
}
/*---------- article.sorry ----------*/
article.sorry .txt {
  text-align: center;
  line-height: 1.5;
}
/*======================================

スマホ用CSS

=======================================*/
@media screen and (max-width: 640px) {
  /*======================================

common

=======================================*/

  body {
    font-size: 16px;
  }

  /*---------- pcOnly ----------*/

  .pcOnly {
    display: none;
  }

  /*---------- spOnly ----------*/

  .spOnly {
    display: inherit;
  }

  /*======================================

header

=======================================*/

  header:after {
    height: 7px;
    bottom: -7px;
  }

  /*---------- nav ----------*/

  header nav {
    width: 100%;
  }

  /* ul */

  header nav ul li {
    padding: 10px 15px;
    font-size: 15px;
  }

  /*======================================
   
main
  
=======================================*/

  main {
    width: 100%;
    padding: 0 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }
  /*---------- h1 ----------*/

  main h1 span {
    font-size: 28px;
    line-height: 1.2;
  }
  /*---------- h2 ----------*/

  main h2 {
    font-size: 20px;
  }
}
